import React, { FC } from 'react';
import { ColumnsProps, DefaultSortProps, FilterType, getExtraData, OrderType, SearchDataGrid, SearchViewProps, WindowComponent } from 'summer';
import { CLASS_NAME, ReinfCFOPConfig } from '../../../../models/dtos/mlf/reinfCFOPConfig';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'cfop.codigo',
    order: OrderType.ASC,
  },
];

const ReinfCFOPSearchView: FC<SearchViewProps<ReinfCFOPConfig>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'cfop.codigo',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'cfop.descricao',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
  ];
  return (
    <WindowComponent title="EFD-Reinf - Configuração de C.F.O.P." wikiId={3666}>
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        defaultSorted={defaultSorted}
        dtoClassName={CLASS_NAME}
        extraData={getExtraData(props, 'ReinfCFOPEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default ReinfCFOPSearchView;
